import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { environment } from '../../../environments/environment';

@Injectable()
export class InventoryManagementService {

  constructor(private http: HttpClient) { }

  productlist(data): Observable<any> {
    return this.http.post<any>(`${environment.base_url}/productlist/`, data);
  }

  productDropdownData(): Observable<any> {
    return this.http.get<any>(`${environment.base_url}/product_only_list/`);
  }

  getDashoardInfo(): Observable<any> {
    return this.http.get<any>(`${environment.base_url}/dashboard/`);
  }

  createProduct(data): Observable<any> {
    return this.http.post<any>(`${environment.base_url}/create_product/`, data);
  }

  updateProduct(data, id): Observable<any> {
    return this.http.patch<any>(`${environment.base_url}/create_product/${id}/`, data);
  }

  getVendors(data): Observable<any> {
    return this.http.post<any>(`${environment.base_url}/vendor/listview/`, data);
  }

  getPackSize(data): Observable<any> {
    return this.http.post<any>(`${environment.base_url}/pack/listview/`, data);
  }

  getCategories(): Observable<any> {
    return this.http.get<any>(`${environment.base_url}/categorieslistNoPagnination/`);
  }

  deleteProduct(id): Observable<any> {
    return this.http.delete<any>(`${environment.base_url}/create_product/${id}/`);
  }

  getProduct(id): Observable<any> {
    return this.http.get<any>(`${environment.base_url}/product/${id}/`);
  }

  adjustQuantity(data): Observable<any> {
    return this.http.post<any>(`${environment.base_url}/adjust_quantity/`, data);
  }

  salesPriceChange(data): Observable<any> {
    return this.http.post<any>(`${environment.base_url}/adjust_product_data/modify_salesprice/`, data);
  }

  statusChange(data): Observable<any> {
    return this.http.post<any>(`${environment.base_url}/adjust_product_data/change_active_status/`, data);
  }
}
