import { Component, OnInit } from '@angular/core';
import { Title } from '@angular/platform-browser';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { ToastrService } from 'ngx-toastr';
import { Router, ActivatedRoute } from '@angular/router';
import { InventoryManagementService } from '../../../shared/services/inventory-management.service';
import { Validations } from '../../../shared/validators/Validations';
import { DatePipe } from '@angular/common';

@Component({
  selector: 'app-adjust-quantity',
  templateUrl: './adjust-quantity.component.html',
})
export class AdjustQuantityComponent implements OnInit {

  addForm: FormGroup;
  submitted: boolean = false;
  productId: string = '';
  totalQunatity: number = 0;
  redirect: number = 0;

  filterVendor: any;
  filterCategory: any;
  filterProduct: any;
  filterQuantity: any;
  tableLimit: number = 10;
  firstTime: boolean = false;
  searchValue: string = "";

  constructor(
    private titleService: Title,
    private inventoryManagementService: InventoryManagementService,
    private toastr: ToastrService,
    private router: Router,
    private route: ActivatedRoute,
    private datePipe: DatePipe
  ) { }

  ngOnInit(): void {
    this.titleService.setTitle("Add Inventory");

    this.addForm = new FormGroup({
      date: new FormControl('', [
        Validators.required
      ]),
      new_price: new FormControl('', [
        Validators.required
      ]),
      purchase_price: new FormControl('', [
        Validators.required
      ]),
      new_purchase_price: new FormControl('', [
        Validators.required
      ]),
      sale_price: new FormControl('', [
        Validators.required
      ]),
      quantity: new FormControl('', [
        Validators.required
      ]),
      available_quantity: new FormControl(''),
      name: new FormControl(''),
      total: new FormControl(''),
      note: new FormControl('')
    });

    this.getProductData();
  }

  getProductData() {
    this.route.queryParams.subscribe(param => {
      this.productId = param.id;
      this.redirect = param.redirect;
      this.filterVendor = param.vendor;
      this.filterCategory = param.category;
      this.filterProduct = param.product;
      this.filterQuantity = param.quantity;
      this.tableLimit = param.limit;
      this.firstTime = param.firstTime;
      this.searchValue = param.search;
    });

    this.inventoryManagementService.getProduct(this.productId).subscribe(response => {
      let resData = JSON.parse(JSON.stringify(response));

      if (resData.status_code == 200) {
        var data = resData.data;

        this.addForm.patchValue({
          name: data.name,
          available_quantity: data.available_quantity,
          date: new Date(data.updated_on),
          purchase_price: data.purchase_cost,
          new_purchase_price: data.purchase_cost,
          sale_price: data.sale_price,
          new_price: data.sale_price
        });

        this.totalQunatity = data.available_quantity;

      } else {
        this.toastr.error('', resData.message);
      }
    },
      err => {
        this.toastr.error('', err.error.message);
      });
  }

  submitAddForm() {
    this.submitted = true;

    var formData = new FormData();
    formData.append("product_id", this.productId);
    formData.append("date", this.datePipe.transform(this.addForm.controls.date.value, "yyyy-MM-dd"));
    formData.append("new_selling_price", this.addForm.controls.new_price.value);
    formData.append("quantity", this.addForm.controls.quantity.value);
    formData.append("new_purchase_price", this.addForm.controls.new_purchase_price.value);
    formData.append("notes", this.addForm.controls.note.value);

    if (this.addForm.valid) {
      this.inventoryManagementService.adjustQuantity(formData).subscribe(response => {
        let resData = JSON.parse(JSON.stringify(response));

        if (resData.status_code == 200) {
          if (this.redirect == 0) {
            this.router.navigate(['/inventory-management/edit-inventory'], { queryParams: { id: this.productId, vendor: this.filterVendor, category: this.filterCategory, product: this.filterProduct, stock: this.filterQuantity, limit: this.tableLimit, firstTime: true, search: this.searchValue } }).then(() => {
              this.toastr.success('', "Quantity adjusted successfully!");
            });
          } else {
            this.router.navigate(['/inventory-management'], { queryParams: { vendor: this.filterVendor, category: this.filterCategory, product: this.filterProduct, stock: this.filterQuantity } }).then(() => {
              this.toastr.success('', "Quantity adjusted successfully!");
            });
          }

        } else {
          this.toastr.error('', resData.message);
        }
      },
        err => {
          this.toastr.error('', err.error.message);
        });

    }
  }

  chnageQunatity() {
    this.totalQunatity = this.addForm.controls.available_quantity.value + this.addForm.controls.quantity.value;
  }

}
