import { Component, OnInit } from '@angular/core';
import { Title } from '@angular/platform-browser';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { ToastrService } from 'ngx-toastr';
import { Router, ActivatedRoute } from '@angular/router';
import { InventoryManagementService } from '../../../shared/services/inventory-management.service';
import { Validations } from '../../../shared/validators/Validations';
import { RxwebValidators } from "@rxweb/reactive-form-validators";
import { DatePipe } from '@angular/common';

@Component({
  selector: 'app-add-inventory',
  templateUrl: './add-inventory.component.html',
})
export class AddInventoryComponent implements OnInit {
  
  addForm: FormGroup;
  submitted: boolean = false;
  categoryData: any;
  vendorData: any;
  packSizeData: any;
  selectedcategory: string = '';
  selectedVendor: string = '';
  selectedPackSize: string = '';
  barcode_imageUrl: string = '';
  selectedDate: string = "2020-10-20";

  filterVendor: any;
  filterCategory: any;
  filterProduct: any;
  filterQuantity: any;
  tableLimit: number = 10;
  
  constructor(
    private titleService: Title,
    private inventoryManagementService:InventoryManagementService,
    private toastr: ToastrService,
    private router:Router, 
    private route:ActivatedRoute,
    private datePipe: DatePipe
  ) {}

  ngOnInit(): void {
    this.selectedDate = this.datePipe.transform(new Date(), 'yyyy-MM-dd');

    this.titleService.setTitle("Add Inventory");

    this.addForm = new FormGroup({
      name: new FormControl('', [
        Validators.required
      ]),
      category: new FormControl('', [
        Validators.required
      ]),
      vendor: new FormControl('', [
        Validators.required
      ]),
      // pack_size: new FormControl('', [
      //   Validators.required
      // ]),
      available_quantity: new FormControl('', [
        Validators.required
      ]),
      updated_on: new FormControl('', [
        Validators.required
      ]),
      low_stock_qauntity: new FormControl('', [
        Validators.required
      ]),
      location: new FormControl('', [
        // Validators.required
      ]),
      sale_price: new FormControl('', [
        Validators.required
      ]),
      description: new FormControl('', [
        // Validators.required
      ]),
      purchase_cost: new FormControl('', [
        Validators.required
      ]),
      purchase_info: new FormControl('', [
        // Validators.required
      ]),
      barcode: new FormControl('', [
        Validators.required
      ]),
      item_number: new FormControl('', [
        Validators.required
      ]),
      barcode_image: new FormControl('', [
          RxwebValidators.extension({extensions:["png","jpg","jpeg","emf","jpe","jfif","bmp","gif","wmf","dib","tif","tiff",'pdf']
          })
      ]),
      barcode_imageSource: new FormControl(''),
      weight: new FormControl('', [
        Validators.required
      ]),
    });

    this.route.queryParams.subscribe(param => {
      this.filterVendor = param.vendor;
      this.filterCategory = param.category;
      this.filterProduct = param.product;
      this.filterQuantity = param.quantity;
      this.tableLimit = param.limit;
    });

    this.getProductData();
  }

  getProductData(){
  	this.inventoryManagementService.getVendors("").subscribe(response => {
        let resData = JSON.parse(JSON.stringify(response));

        if(resData.status_code == 200){
          this.vendorData = resData.data;
        }else{
          this.toastr.error('', resData.message);
        }
      },
        err => {
          this.toastr.error('', err.error.message);
        });

        this.inventoryManagementService.getPackSize("").subscribe(response => {
        let resData = JSON.parse(JSON.stringify(response));

        if(resData.status_code == 200){
          this.packSizeData = resData.data;
        }else{
          this.toastr.error('', resData.message);
        }
      },
        err => {
          this.toastr.error('', err.error.message);
        });

        this.inventoryManagementService.getCategories().subscribe(response => {
        let resData = JSON.parse(JSON.stringify(response));

        if(resData.status_code == 200){
          this.categoryData = resData.data;
        }else{
          this.toastr.error('', resData.message);
        }
      },
        err => {
          this.toastr.error('', err.error.message);
        });
  }

  onBarcodeFileChange(event)
  {
	  if (event.target.files.length > 0) {
	    const file = event.target.files[0];
	    if(file){
	      this.addForm.patchValue({
	        barcode_imageSource: file
	      });
	      const reader = new FileReader();
	      reader.onloadend = (event: any) => {
	        this.barcode_imageUrl = event.target.result;
	      };
	      reader.readAsDataURL(event.target.files[0]);
	    }
	  }
	}

	submitAddForm(){
    this.submitted = true;

      var formData = new FormData();
      formData.append("name", this.addForm.controls.name.value);
      formData.append("category", this.addForm.controls.category.value);
      formData.append("vendor", this.addForm.controls.vendor.value);
      // formData.append("pack_size", this.addForm.controls.pack_size.value);
      formData.append("available_quantity", this.addForm.controls.available_quantity.value);
      formData.append("updated_on", this.addForm.controls.updated_on.value);
      formData.append("low_stock_qauntity", this.addForm.controls.low_stock_qauntity.value);
      formData.append("location", this.addForm.controls.location.value);
      formData.append("sale_price", this.addForm.controls.sale_price.value);
      formData.append("description", this.addForm.controls.description.value);
      formData.append("purchase_cost", this.addForm.controls.purchase_cost.value);
      formData.append("purchase_info", this.addForm.controls.purchase_info.value);
      // formData.append("barcode_image", this.addForm.get('barcode_imageSource').value);
      formData.append("barcode_string", this.addForm.controls.barcode.value);
      formData.append("weight", this.addForm.controls.weight.value);
      formData.append("item_no", this.addForm.controls.item_number.value);
      
      if (this.addForm.valid) {
      this.inventoryManagementService.createProduct(formData).subscribe(response => {
        let resData = JSON.parse(JSON.stringify(response));

        if(resData.status_code == 200){
          this.router.navigate(['inventory-management'], { queryParams: {vendor: this.filterVendor, category: this.filterCategory, product: this.filterProduct, stock: this.filterQuantity, limit: this.tableLimit, firstTime: true} }).then(() => {
            this.toastr.success('', "Product added successfully!");
          });
        }else{
          this.toastr.error('', resData.message);
        }
      },
        err => {
          this.toastr.error('', err.error.message);
        });

    }
  }

  changeBarcodeValue(){
    this.addForm.patchValue({
      item_number: this.addForm.controls.barcode.value.substr(-6)
    });
  }

}
