import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { AddInventoryComponent } from './add-inventory/add-inventory.component';
import { AdjustQuantityComponent } from './adjust-quantity/adjust-quantity.component';
import { EditInvertoryComponent } from './edit-invertory/edit-invertory.component';
import {InventoryManagementComponent} from './inventory-management.component';


const routes: Routes = [ 
  {
    path: '',
    data: {
      title: 'Inventory Management'
    },
    children: [
      {
        path: '',
        component: InventoryManagementComponent,
        data: {
          title: 'List'
        }
      },
      {
        path: 'add-inventory',
        component: AddInventoryComponent,
        data: {
          title: 'Add'
        }
      },
      {
        path: 'edit-inventory',
        component: EditInvertoryComponent,
        data: {
          title: 'Edit'
        }
      },
      {
        path: 'adjust-quantity',
        component: AdjustQuantityComponent,
        data: {
          title: 'Adjust Quantity'
        }
      },
    ]
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class InventoryManagementRoutingModule { }
