import { Component, OnInit, ViewChild } from '@angular/core';
import { Title } from '@angular/platform-browser';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { ToastrService } from 'ngx-toastr';
import { Router, ActivatedRoute } from '@angular/router';
import { InventoryManagementService } from '../../../shared/services/inventory-management.service';
import { Validations } from '../../../shared/validators/Validations';
import { RxwebValidators } from "@rxweb/reactive-form-validators";
import { environment } from '../../../../environments/environment';
import { DatePipe } from '@angular/common';
import { ModalDirective } from 'ngx-bootstrap/modal';

@Component({
  selector: 'app-edit-invertory',
  templateUrl: './edit-invertory.component.html',
})
export class EditInvertoryComponent implements OnInit {

  @ViewChild('salespricemodal') public salespricemodal: ModalDirective;

  editForm: FormGroup;
  submitted: boolean = false;
  categoryData: any;
  vendorData: any;
  packSizeData: any;
  old_sales_price: string = "";
  new_sales_price: string = "";
  selectedcategory: string = '';
  selectedVendor: string = '';
  selectedPackSize: string = '';
  barcode_imageUrl: string = '';
  productId: string = '';
  selectedDate: string = "2020-10-20";
  filterVendor: any;
  filterCategory: any;
  filterProduct: any;
  filterQuantity: any;
  tableLimit: number = 10;
  firstTime: boolean = false;
  searchValue: string = "";

  constructor(
    private titleService: Title,
    private inventoryManagementService: InventoryManagementService,
    private toastr: ToastrService,
    private router: Router,
    private route: ActivatedRoute,
    private datePipe: DatePipe
  ) { }

  ngOnInit(): void {
    this.selectedDate = this.datePipe.transform(new Date(), 'yyyy-MM-dd');

    this.titleService.setTitle("Edit Inventory");

    this.editForm = new FormGroup({
      name: new FormControl('', [
        Validators.required
      ]),
      status: new FormControl(''),
      category: new FormControl('', [
        Validators.required
      ]),
      vendor: new FormControl('', [
        Validators.required
      ]),
      // pack_size: new FormControl('', [
      //   Validators.required
      // ]),
      available_quantity: new FormControl('', [
        Validators.required
      ]),
      updated_on: new FormControl('', [
        Validators.required
      ]),
      low_stock_qauntity: new FormControl('', [
        Validators.required
      ]),
      location: new FormControl('', [
        // Validators.required
      ]),
      sale_price: new FormControl('', [
        Validators.required
      ]),
      description: new FormControl('', [
        // Validators.required
      ]),
      purchase_cost: new FormControl('', [
        Validators.required
      ]),
      purchase_info: new FormControl('', [
        // Validators.required
      ]),
      barcode_image: new FormControl('', [
        RxwebValidators.extension({
          extensions: ["png", "jpg", "jpeg", "emf", "jpe", "jfif", "bmp", "gif", "wmf", "dib", "tif", "tiff", 'pdf']
        })
      ]),
      barcode_imageSource: new FormControl(''),
      barcode: new FormControl('', [
        Validators.required
      ]),
      item_number: new FormControl('', [
        Validators.required
      ]),
      weight: new FormControl('', [
        Validators.required
      ])
    });

    this.getProductData();
  }

  getProductData() {
    this.route.queryParams.subscribe(param => {
      this.productId = param.id;
      this.filterVendor = param.vendor;
      this.filterCategory = param.category;
      this.filterProduct = param.product;
      this.filterQuantity = param.quantity;
      this.tableLimit = param.limit;
      this.firstTime = param.firstTime;
      this.searchValue = param.search;
    });

    this.inventoryManagementService.getVendors("").subscribe(response => {
      let resData = JSON.parse(JSON.stringify(response));

      if (resData.status_code == 200) {
        this.vendorData = resData.data;
      } else {
        this.toastr.error('', resData.message);
      }
    },
      err => {
        this.toastr.error('', err.error.message);
      });

    this.inventoryManagementService.getPackSize({ product_id: this.productId }).subscribe(response => {
      let resData = JSON.parse(JSON.stringify(response));

      if (resData.status_code == 200) {
        this.packSizeData = resData.data;
      } else {
        this.toastr.error('', resData.message);
      }
    },
      err => {
        this.toastr.error('', err.error.message);
      });

    this.inventoryManagementService.getCategories().subscribe(response => {
      let resData = JSON.parse(JSON.stringify(response));

      if (resData.status_code == 200) {
        this.categoryData = resData.data;
      } else {
        this.toastr.error('', resData.message);
      }
    },
      err => {
        this.toastr.error('', err.error.message);
      });

    this.inventoryManagementService.getProduct(this.productId).subscribe(response => {
      let resData = JSON.parse(JSON.stringify(response));

      if (resData.status_code == 200) {
        var data = resData.data;
        this.old_sales_price = data.sale_price;
        this.editForm.patchValue({
          name: data.name,
          status: data.is_active ? "Active" : "Inactive",
          available_quantity: data.available_quantity,
          updated_on: data.updated_on,
          low_stock_qauntity: data.low_stock_qauntity,
          location: data.location,
          sale_price: data.sale_price,
          description: data.description,
          purchase_cost: data.purchase_cost,
          purchase_info: data.purchase_info,
          barcode: data.barcode_string,
          item_number: data.barcode_string.substr(-6),
          weight: data.weight
        });

        this.selectedcategory = data.category;
        this.selectedVendor = data.vendor;
        // this.selectedPackSize = data.pack_size;
        if (data.barcode_image != null) {
          this.barcode_imageUrl = environment.base_url + data.barcode_image;
        }

      } else {
        this.toastr.error('', resData.message);
      }
    },
      err => {
        this.toastr.error('', err.error.message);
      });
  }

  onBarcodeFileChange(event) {
    if (event.target.files.length > 0) {
      const file = event.target.files[0];
      if (file) {
        this.editForm.patchValue({
          barcode_imageSource: file
        });
        const reader = new FileReader();
        reader.onloadend = (event: any) => {
          this.barcode_imageUrl = event.target.result;
        };
        reader.readAsDataURL(event.target.files[0]);
      }
    }
  }

  submitEditForm() {

    this.submitted = true;

    var formData = new FormData();
    formData.append("name", this.editForm.controls.name.value);
    formData.append("category", this.editForm.controls.category.value);
    formData.append("vendor", this.editForm.controls.vendor.value);
    // formData.append("pack_size", this.editForm.controls.pack_size.value);
    formData.append("available_quantity", this.editForm.controls.available_quantity.value);
    formData.append("updated_on", this.editForm.controls.updated_on.value);
    formData.append("low_stock_qauntity", this.editForm.controls.low_stock_qauntity.value);
    formData.append("location", this.editForm.controls.location.value);
    formData.append("sale_price", this.editForm.controls.sale_price.value);
    formData.append("description", this.editForm.controls.description.value);
    formData.append("purchase_cost", this.editForm.controls.purchase_cost.value);
    formData.append("purchase_info", this.editForm.controls.purchase_info.value);
    if (this.editForm.get('barcode_imageSource').value) {
      formData.append("barcode_image", this.editForm.get('barcode_imageSource').value);
    }
    formData.append("barcode_string", this.editForm.controls.barcode.value);
    formData.append("weight", this.editForm.controls.weight.value);
    formData.append("item_no", this.editForm.controls.item_number.value);

    if (this.editForm.valid) {
      this.inventoryManagementService.updateProduct(formData, this.productId).subscribe(response => {
        let resData = JSON.parse(JSON.stringify(response));

        if (resData.status_code == 200) {
          this.router.navigate(['inventory-management'], { queryParams: { vendor: this.filterVendor, category: this.filterCategory, product: this.filterProduct, stock: this.filterQuantity, limit: this.tableLimit, firstTime: true, search: this.searchValue } }).then(() => {
            this.toastr.success('', "Product updated successfully!");
          });
        } else {
          this.toastr.error('', resData.message);
        }
      },
        err => {
          this.toastr.error('', err.error.message);
        });

    }
  }

  changeBarcodeValue() {
    this.editForm.patchValue({
      item_number: this.editForm.controls.barcode.value.substr(-6)
    });
  }

  salesPriceChange() {
    var data = {
      product_id: this.productId,
      new_sales_price: this.new_sales_price
    }
    this.inventoryManagementService.salesPriceChange(data).subscribe(response => {
      let resData = JSON.parse(JSON.stringify(response));

      if (resData.status_code == 200) {
        this.salespricemodal.hide();
        this.toastr.success('', "Product added successfully!");
        this.getProductData();
        this.old_sales_price = '';
        this.new_sales_price = '';
      } else {
        this.salespricemodal.hide();
        this.toastr.error('', resData.message);
        this.old_sales_price = '';
        this.new_sales_price = '';
      }
    });
  }
}
